  animation: breathe-and-jump 3s linear infinite;
  cursor: pointer;
  z-index: 1;
}

.chat-bot::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  width: calc(100% + 3px);
  height: calc(100% + 2px);
  background-color: #18c;
  background: linear-gradient(to bottom, #5fc, #1a8);
  background: -webkit-linear-gradient(to bottom, #5cf, #18c);
