.pSize p {
  float: left;
  margin: 0;
}

.pSize p::after {
  content: "";
  display: table;
  clear: both;
}

.pSize .sizeBtn {
  border: 1px solid #ddd;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  border-radius: 50px;
  background-color: transparent;
  transition: all 0.5s ease;
  margin: 5px;
  line-height: 40px;
  padding: 0;
}

.pSize .size-radio {
  position: static;
  right: 0px;
  bottom: 0px;
  border: 1px solid #ddd;
  width: 40px;
  height: 40px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  border-radius: 50px;
  background-color: transparent;
  transition: all 0.5s ease;
  line-height: 0px;
  margin: 0 5px;
  cursor: pointer;
  z-index: 0;
  font-size: 12px;
}

.pSize .color-radio {
  position: static;
  right: 0px;
  bottom: 0px;
  border: 1px solid #ddd;
  width: 80px;
  height: 40px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  border-radius: 2px;
  line-height: 0px;
  margin: 0 5px;
  cursor: pointer;
  z-index: 0;
  font-size: 12px;
}

.flex-center {
  display: flex;
  align-items: center;
}

.pSize .color-radio:hover,
.pSize .size-radio:hover {
  background-color: royalblue;
  color: #fff;
  transition: all 0.5s ease;
}

.pSize .radio-input:checked + .color-radio,
.pSize .radio-input:checked + .size-radio {
  background-color: #212121;
  color: #fff;
  transition: all 0.5s ease;
}

.pSize .btnGroup {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 20px 0;
}

.psize .btnGroup {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  border-radius: 10px;
}

