.image-card{
  position: relative;
  height: 400px;
  width: 300px;
  overflow: hidden;
  border-radius: 3px;
  transition: all .4s;
}

.image-card:hover{
  box-shadow: 1px 1px 6px 1px rgba(0,0,0,.3);
}

.image-card:before{
  content: '';
  position: absolute;
  top: 0;
  left: 180%;
  height: 100%;
  width: 75%;
  background: rgba(255,255,255,.1);
  z-index: 1;
  transform: skew(45deg);
  opacity: .7;
  pointer-events: none;
  transition: .5s;
}

.image-card:hover:before{
  left: -180%;
}

.image-card img{
  height: 100%;
  width: 100%;
  filter: grayscale(100%);
  transition: 2s;
}

.image-card:hover img{
  filter: grayscale(0%);
  transform: scale(1.1);
}

.image-card h2{
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #e74c3c;
  padding: 10px 0;
  color: #f9d6d2;
  font-family: verdana;
  border: 1px solid #e64533;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transform: perspective(500px) rotateY(-90deg);
  transform-origin: right;
  transition: all .5s;
}

.image-card:hover h2{
  transform: perspective(500px) rotateY(0deg);
}

.image-card .ulnew{
  position: absolute;
  list-style: none;
  top: 0;
  left: -30px;
  background: rgba(255,255,255,0);
}

.image-card .ulnew .linew{
  height: 40px;
  width: 40px;
  line-height: 40px;
  background: #333;
  cursor: pointer;
  transition: all .5s;
  transform: perspective(800px) rotateY(90deg);
  transform-origin: left;
  border-bottom: 1px solid rgba(0,0,0,.1);
}

.image-card:hover .ulnew .linew{
  transform: perspective(800px) rotateY(0deg);
}

.image-card:hover .ulnew .linew:nth-child(1){
  transition-delay: .2s;
}

.image-card:hover .ulnew .linew:nth-child(2){
  transition-delay: .6s;
}

.image-card:hover .ulnew .linew:nth-child(3){
  transition-delay: .8s;
}

.image-card:hover .ulnew li:nth-child(4){
  transition-delay: 1s;
}

.image-card .ulnew .linew i{
  font-size: 20px;
  color: tomato !important;
  background: rgba(255,255,255,0);
  text-align: center !important;
  margin-left: 10px;
}

.ulnew .linew span{
  position: absolute;
  left: 10px;
  font-size: 14px;
  text-transform: uppercase;
  background: #333;
  color: tomato;
  border-bottom: 1px solid rgba(0,0,0,.1);
  padding: 0 10px;
  opacity: 0;
  z-index: -1;
  pointer-events: none;
  transition: all .5s;
}

.ulnew .linew:hover span{
  left: 30px;
  opacity: 1;
  pointer-events: auto;
}

