.ulContact {
  margin-top: 0px;
  /* margin-right: 50px; */
  display: flex;
  flex-direction: column;
}

.ulContact .liContact {
  list-style: none;
  margin: 5px 0;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.circle {
  width: 50px;
  height: 50px;
  background: #cb8364;
  border-radius: 50%;
  margin-right: 15px;
  line-height: 50px;
  text-align: center;
  color: #fff;
  font-size: 0.8rem;
}

.contact-section {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 1fr);
  /* width: 100%; */
  background: white;
  padding: 40px;
  border-radius: 5px;
  height: 500px;
  margin-left: 300px;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}

.contact-section textarea  {
  grid-column: 1 / -1;
  resize: none;
  width: 100%;
  height: 140px;
  border-radius: 10px;
}

.contact-section input[type="tel"]  {
  grid-column: 1 / -1;
  resize: none;
  width: 100%;
  height: 42px;
}

.contact-section input[type=text],
.contact-section input[type=tel],
.contact-section input[type=email]{
  border-radius: 20px;
  height: 42px;
}

.contact-section textarea,
.contact-section input[type=text],
.contact-section input[type=tel],
.contact-section input[type=email] {
  padding: 5px;
  outline: none;
  border: 2px solid grey;
  color: #fff;
}

.buttonContainer {
  background-color: #cb8364;
  border: none;
  border-radius: 50px;
  padding: -20px !;
}

